import React, { useEffect } from "react";
import { useState } from "react";
import firebase from './Firebase'

export const Context = React.createContext();

export const Provider = (props) => {
  let [editWindowIsVisible, setEditWindowIsVisible] = useState(false);
  let [firestoreData, setFirestoreData] = useState([])
  let [currentNote, setCurrentNote] = useState({});
  let [createFlag, setCreateFlag] = useState();
  let [currentUser, setCurrentUser] = useState();
  let [collection, setCollection] = useState(null);

  

  /* useEffect(() => {
    fetchData();
  }, [editWindowIsVisible]) */

  useEffect(() => {
    firebase.auth().onAuthStateChanged((user) => {
      setCurrentUser(user);
      setCollection(firebase.auth().currentUser.uid);
      if (firebase.auth().currentUser !== null) {
        console.log(firebase.auth().currentUser.uid);
      }
    });
  }, [])

  return (
    <Context.Provider value={{ editWindowIsVisible, setEditWindowIsVisible, firestoreData, setFirestoreData, currentNote, setCurrentNote, createFlag, setCreateFlag, currentUser, collection, setCollection }}>
      {props.children}
    </Context.Provider>
  );
};

export default Provider;
