import firebase from '../Firebase'

const fetchData = async (collection, setFirestoreData) => {
    const db = firebase.firestore();
    if (collection !== null) {
      const data = await db.collection(collection).get();
      setFirestoreData(data.docs.map(doc => ({ ...doc.data(), id: doc.id })));
    }
  }

const updateNote = async ( id, data) => {
    const db = firebase.firestore();
    let collection = firebase.auth().currentUser.uid;
    let auxData = { ...data };
    delete auxData['id'];
    await db.collection(collection).doc(id).set(auxData);
}

const deleteNote = async ( id) => {
    const db = firebase.firestore();
    let collection = firebase.auth().currentUser.uid;
    await db.collection(collection).doc(id).delete();
}


const createNote = async ( data) => {
    const db = firebase.firestore();
    let collection = firebase.auth().currentUser.uid;
    console.log(data);
    await db.collection(collection).add(data);
}

const createUser = async (e, email, password) => {
    e.preventDefault();
    let response;
    await firebase.
        auth().
        createUserWithEmailAndPassword(email, password).
        then(cred => { response = { sucess: true, code: '', message: '' }; }).
        catch(err => { response = { sucess: false, code: err.code, message: err.message }; });
    return response;
}


const signin = async (e, email, password) => {
    e.preventDefault();
    let response;
    await firebase.
        auth().
        signInWithEmailAndPassword(email, password).
        then(cred => { response = { sucess: true, code: '', message: '' }; }).
        catch(err => { response = { sucess: false, code: err.code, message: err.message }; });
    return response;
}

const signout = async (e, history) => {
    e.preventDefault();
    await firebase.auth().signOut().then(message => console.log(message)).catch(err => console.log(err));
    history.push('/login');
}


const resetPassword = (emailAddress, setMessage) => {
    console.log(emailAddress)
    firebase.auth().sendPasswordResetEmail(emailAddress).then(function () {
        setMessage('A reset password e-mail was sent to ' + emailAddress);
    }).catch(function (error) {
        setMessage(error.message);
    });
}

const onClickSignInBtn = async (e, emailState, passwordState, history, setErrorMessage) => {
    e.preventDefault();
    let res = await signin(e, emailState, passwordState);
    if (res.sucess) {
        history.push('/')
    } else {
        setErrorMessage(res.message);
    }
}

const onClickSignupBtn = async (e, emailState, passwordState, history, setErrorMessage) => {
    e.preventDefault();
    let res = await createUser(e, emailState, passwordState);
    if (res.sucess) {
        history.push('/')
    } else {
        setErrorMessage(res.message);
    }
}

const onClickSignoutBtn = async (e, history, setFirestoreData) => {
   await signout(e, history);
   setFirestoreData([]);
}




export { updateNote, deleteNote, createNote, createUser, signin, signout, onClickSignInBtn, onClickSignupBtn, onClickSignoutBtn, resetPassword, fetchData }

