import React, {useContext} from 'react'
import styles from './Header.module.css'
import facebook from './images/media/facebook.webp'
import linkedin from './images/media/linkedin.webp'
import instagram from './images/media/instagram.webp'
import { onClickSignoutBtn } from '../../functions/functions'
import { useHistory } from "react-router-dom";
import {useLocation} from 'react-router-dom'
import { Context } from "../../Context.js";

const Header = () => {
    const history = useHistory();
    let {  setFirestoreData } = useContext(Context);
    

    return (
        <div className={styles.container}>
            <div className={styles.overlay}></div>
            <div className={styles.logo}></div>
            <div className={styles.mediaButtons}>
                <a href="https://www.facebook.com/NONGagro/">
                    <img src={facebook}></img>
                </a>
                <a href="https://www.linkedin.com/company/33249714/admin/updates/">
                    <img src={linkedin}></img>
                </a>
                <a href="https://www.instagram.com/nong.agro/">
                    <img src={instagram}></img>
                </a>
            </div>
            {useLocation().pathname === '/' ?
                <div className={styles.logoutButton}
                 onClick={(e) => onClickSignoutBtn(e, history, setFirestoreData)}>
                    <i class="fas fa-sign-out-alt"></i>
                </div> : ''}
           <div className={styles.logoutLabel} >
                <h1>LOG OUT</h1>
            </div>
        </div>
    )
}

export default Header